import React from "react";
import { Parallax } from "react-parallax";
import Face3 from "../../../assets/face-3.jpg";

const BackgroundImageFace3 = () => {
  return (
    <Parallax bgImage={Face3} strength={600}>
      <div style={{ height: "50vh" }}></div>
      <a href="/bid"> </a>
    </Parallax>
  );
};

export default BackgroundImageFace3;
