import React, { useEffect, useState } from "react";
import "./box-animate-1.css";

const BoxAnimate1 = () => {
  const [isAnimated, setIsAnimated] = useState(false);
  const [emailEntered, setEmailEntered] = useState(false);

  const handleSubmitForm = () => {
    const email = document.querySelector('input[name="email"]').value;
    console.log(`Email submitted: ${email}`);
    setEmailEntered(true);

    setTimeout(() => {
      setEmailEntered(false);
    }, 2000);
  };

  const handleEmailSubmit = (event) => {
    event.preventDefault();
    handleSubmitForm();
  };

  const handleSvgButtonClick = () => {
    handleSubmitForm();
  };
  const handleThankYouClick = () => {
    setEmailEntered(false);
  };

  useEffect(() => {
    const handleScroll = () => {
      const boxes = document.querySelectorAll(".bid-type-box");

      if (boxes.length > 0) {
        let shouldAnimate = false;

        boxes.forEach((box) => {
          const boxTop = box.getBoundingClientRect().top;
          if (boxTop < window.innerHeight / 2) {
            shouldAnimate = true;
          }
        });

        setIsAnimated(shouldAnimate);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <div className={`box-container ${isAnimated ? "scroll-animated" : ""}`}>
      {/* Box 1 */}
      <div className="bid-type-box box-1-animate">
        <img
          src={require("../../../assets/box-4-animation-image-1.jpg")}
          alt="Box 1"
        />
        <div className="box-content">
          <div className="title">
            <span className="luxury-title">Luxury Sports Car</span>
          </div>
          <p className="bidtype-box-text">
            Sleek, powerful, and meticulously maintained luxury sports car. Low
            mileage, excellent condition. Bid now for the thrill of the open
            road!
            <br></br>
            <br></br>
            Sleek, powerful, and meticulously maintained luxury sports car.
          </p>
          <span className="bid-link">
            <a href="/bid-type/frontend/bid">Bid Now</a>
          </span>
        </div>
      </div>

      {/* Box 2 */}
      <div className="bid-type-box2 box-2-animate">
        <img
          src={require("../../../assets/box-4-animation-image-2.jpg")}
          alt="Box 1"
        />
        <div className="box-content2">
          <div className="title2">
            <span className="luxury-title2">Luxury Sports Car</span>
          </div>
          <p className="bidtype-box-text2">
            Sleek, powerful, and meticulously maintained luxury sports car. Low
            mileage, excellent condition. Bid now for the thrill of the open
            road!
            <br></br>
            <br></br>
            Sleek, powerful, and meticulously maintained luxury sports car.
          </p>
          <span className="bid-link2">
            <a href="/bid-type/frontend/bid">Bid Now</a>
          </span>
        </div>{" "}
      </div>

      {/* Box 3 */}
      <div className="bid-type-box box-3-animate">
        <img
          src={require("../../../assets/box-4-animation-image-3.jpg")}
          alt="Box 1"
        />
        <div className="box-content">
          <div className="title">
            <span className="luxury-title">Luxury Sports Car</span>
          </div>
          <p className="bidtype-box-text">
            Sleek, powerful, and meticulously maintained luxury sports car. Low
            mileage, excellent condition. Bid now for the thrill of the open
            road!
            <br></br>
            <br></br>
            Sleek, powerful, and meticulously maintained luxury sports car.
          </p>
          <span className="bid-link">
            <a href="/bid-type/frontend/bid">Bid Now</a>
          </span>
        </div>{" "}
      </div>

      {/* Box 4 */}
      <div className="bid-type-box2 box-4-animate">
        <img
          src={require("../../../assets/box-4-animation-image-4.jpg")}
          alt="Box 1"
        />
        <div className="box-content2">
          <div className="title2">
            <span className="luxury-title2">Luxury Sports Car</span>
          </div>
          <p className="bidtype-box-text2">
            Sleek, powerful, and meticulously maintained luxury sports car. Low
            mileage, excellent condition. Bid now for the thrill of the open
            road!
            <br></br>
            <br></br>
            Sleek, powerful, and meticulously maintained luxury sports car.
          </p>
          <span className="bid-link2">
            <a href="/bid-type/frontend/bid">Bid Now</a>
          </span>
        </div>

        {/* Email Input Box BT */}
        <div className="email-input-box">
          <div className="email-content">
            <span className="email-title">
              Join our exclusive auction community and never miss a deal! Sign
              up now.
            </span>
            {emailEntered ? (
              <p className="thank-you" onClick={handleThankYouClick}>
                Thank you for becoming part of our journey!
              </p>
            ) : (
              <form onSubmit={handleEmailSubmit}>
                <div className="email-input-box1">
                  <input
                    type="email"
                    name="email"
                    placeholder="Enter your email address"
                    className="email-input"
                    required
                  />
                </div>
              </form>
            )}
          </div>
          <button
            type="button"
            className="email-submit-button"
            onClick={handleEmailSubmit}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="64"
              height="50"
              viewBox="0 0 77 63"
              fill="none"
            >
              <path
                d="M73.5 0C74.4283 0 75.3185 0.368749 75.9749 1.02513C76.6313 1.6815 77 2.57174 77 3.5V59.5245C76.9936 60.4437 76.6258 61.3234 75.9762 61.9737C75.3266 62.624 74.4472 62.9927 73.528 63H10.472C9.55085 62.9991 8.66774 62.6325 8.01671 61.9808C7.36569 61.3291 7 60.4457 7 59.5245V56H70V15.05L42 40.25L7 8.75V3.5C7 2.57174 7.36875 1.6815 8.02513 1.02513C8.6815 0.368749 9.57174 0 10.5 0H73.5ZM28 42V49H0V42H28ZM17.5 24.5V31.5H0V24.5H17.5ZM68.481 7H15.519L42 30.835L68.481 7Z"
                fill="#B49662"
              />
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
};

export default BoxAnimate1;
