import React from "react";
import { Parallax } from "react-parallax";
import "./backface-1.css";
import Face1 from "../../../assets/face-1.jpg";

const BackgroundImageFace1 = () => {
  return (
    <Parallax bgImage={Face1} strength={600}>
      <div style={{ height: "50vh" }}></div>
      <a href="/bid">
        <div className="register-name-1">Register Now For Bid/Auction</div>
      </a>
    </Parallax>
  );
};

export default BackgroundImageFace1;