import React, { useState, useRef, useEffect } from 'react';
import './bid-category-drop.css';

function Dropdown({ label, options, selectedOptions, onSelect, isOpen, onToggle, children }) {
  const dropdownRef = useRef();

  const handleCategoryClick = () => {
    onToggle(!isOpen);
  };

  const handleOptionSelect = (option) => {
    onSelect(option);
  };

  useEffect(() => {
    const handleOutsideClick = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        onToggle(false);
      }
    };

    document.addEventListener('mousedown', handleOutsideClick);

    return () => {
      document.removeEventListener('mousedown', handleOutsideClick);
    };
  }, [onToggle]);

  return (
    <div ref={dropdownRef} className="bid-drop-category-container">
      <div className={`bid-drop-category-text ${isOpen ? 'active' : ''}`} onClick={handleCategoryClick}>
        {label}
        <div className="bid-drop-category-icon" onClick={handleCategoryClick}>
          <svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6" fill="none">
            <path d="M9 1L5 5L1 1" stroke="#B49662" strokeLinecap="round" strokeLinejoin="round" />
          </svg>
        </div>
      </div>

      {isOpen && (
        <div className="bid-category-dropdown">
          {options.map((item, idx) => (
            <div key={idx} className="bid-category-dropdown-item" onClick={() => handleOptionSelect(item)}>
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={selectedOptions.includes(item)}
                  onChange={() => handleOptionSelect(item)}
                />
                <span className="custom-checkbox"></span>
              </label>
              <div className="bid-category-dropdown-text">{item}</div>
              <div className="right-icon">{}</div>
            </div>
          ))}
          {children}
        </div>
      )}
    </div>
  );
}

function BidCategoryDrop() {
  const [selectedBrand, setSelectedBrand] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState([]);
  const [selectedPrice, setSelectedPrice] = useState(0);
  const [selectedYear, setSelectedYear] = useState('');
  const [selectedMileageFrom, setSelectedMileageFrom] = useState(1);
  const [selectedMileageTo, setSelectedMileageTo] = useState(100000);
  const [selectedFuel, setSelectedFuel] = useState([]);
  const [selectedTransmission, setSelectedTransmission] = useState('');
  const [activeDropdown, setActiveDropdown] = useState(null);

  const handleDownload = () => {
    const anchor = document.createElement('a');
    anchor.href = 'path/to/your/file.ext';
    anchor.target = '_blank';
    anchor.download = 'downloaded_file.ext';
    document.body.appendChild(anchor);
    anchor.click();
    document.body.removeChild(anchor);
  };

  const handleShare = () => {
    alert('Sharing...');
  };

  const handleDropdownToggle = (dropdownName, isOpen) => {
    setActiveDropdown(isOpen ? dropdownName : null);
  };

  return (
    <div className="bid-category-drop-container">
      <div className="bid-category-search-bar">
        <div className="bid-category-search-bar-left">
          Search:
        </div>
        <div className="bid-category-search-bar-right">
      </div>
      </div>

      <Dropdown
        label="Select Brand"
        options={['Automobiles (Cars)', 'Motorcycles', 'Trucks and Vans', 'Bicycles', 'Public Transportation', 'Watercraft', 'Aircraft', 'Off-Road and Recreational Vehicles', 'Construction and Heavy Equipment', 'Emergency and Special Purpose Vehicles', 'Railroad Vehicles', 'Spacecraft', 'Miscellaneous']}
        selectedOptions={selectedBrand}
        onSelect={(options) => setSelectedBrand(options)}
        isOpen={activeDropdown === 'brand'}
        onToggle={(isOpen) => handleDropdownToggle('brand', isOpen)}
      />

      <Dropdown
        label="Select Category"
        options={['Bilar', 'Motorcyklar', 'Lastbilar och Transportbilar', 'Cyklar', 'Kollektivtrafik', 'Sjöfart', 'Flygplan', 'Terrängfordon och Fritidsfordon', 'Bygg- och Anläggningsfordon', 'Nöd- och Särskilda Fordon', 'Järnvägsfordon', 'Rymdfarkoster', 'Övriga']}
        selectedOptions={selectedCategory}
        onSelect={(options) => setSelectedCategory(options)}
        isOpen={activeDropdown === 'category'}
        onToggle={(isOpen) => handleDropdownToggle('category', isOpen)}
      />

      <Dropdown
        label="Select Price"
        options={[]}
        selectedOptions={selectedPrice}
        onSelect={(option) => setSelectedPrice(option)}
        isOpen={activeDropdown === 'price'}
        onToggle={(isOpen) => handleDropdownToggle('price', isOpen)}
      >
        <input
          type="range"
          min="1"
          max="100000"
          value={selectedPrice}
          onChange={(e) => setSelectedPrice(e.target.value)}
        />
      </Dropdown>

      <Dropdown
        label="Select Year"
        options={['2020', '2021', '2022', '2023']}
        selectedOptions={selectedYear}
        onSelect={(option) => setSelectedYear(option)}
        isOpen={activeDropdown === 'year'}
        onToggle={(isOpen) => handleDropdownToggle('year', isOpen)}
      />

      <Dropdown
        label="Select Mileage"
        options={[]}
        selectedOptions={`${selectedMileageFrom} - ${selectedMileageTo}`}
        onSelect={(option) => {
          const [from, to] = option.split(' - ');
          setSelectedMileageFrom(parseInt(from, 10));
          setSelectedMileageTo(parseInt(to, 10));
        }}
        isOpen={activeDropdown === 'mileage'}
        onToggle={(isOpen) => handleDropdownToggle('mileage', isOpen)}
      >
        <input
          type="range"
          min="1"
          max="100000"
          value={selectedMileageFrom}
          onChange={(e) => setSelectedMileageFrom(e.target.value)}
        />
        <input
          type="range"
          min="1"
          max="100000"
          value={selectedMileageTo}
          onChange={(e) => setSelectedMileageTo(e.target.value)}
        />
      </Dropdown>
      <Dropdown
        label="Select Fuel"
        options={['Diesel', 'Petrol', 'Biogas', 'Electric']}
        selectedOptions={selectedFuel}
        onSelect={(options) => setSelectedFuel(options)}
        isOpen={activeDropdown === 'fuel'}
        onToggle={(isOpen) => handleDropdownToggle('fuel', isOpen)}
      />

      <Dropdown
        label="Select Transmission"
        options={['Automatic', 'Manual']}
        selectedOptions={selectedTransmission}
        onSelect={(option) => setSelectedTransmission(option)}
        isOpen={activeDropdown === 'transmission'}
        onToggle={(isOpen) => handleDropdownToggle('transmission', isOpen)}
      />

      <div className="bid-category-share-download">
        <div className="bid-category-share-download-icon" onClick={handleShare}>
        </div>
        <div className="bid-category-download-action" onClick={handleShare}>
          Share
        </div>
        <div className="bid-category-share-download-icon" onClick={handleDownload}>
        </div>
        <div className="bid-category-download-action" onClick={handleDownload}>
          Download
        </div>
      </div>
    </div>
  );
}

export default BidCategoryDrop;
