import React from "react";
import { Parallax } from "react-parallax";
import Face4 from "../../../assets/face-2.jpg";

const BackgroundImageFace4 = () => {
  return (
    <Parallax bgImage={Face4} strength={600}>
      <div style={{ height: "50vh" }}></div>
    </Parallax>
  );
};

export default BackgroundImageFace4;
