import React from "react";
import "./Box3.css";
import img3 from "../../../assets/luxury sports car 3.jpg";

const Box3 = () => {
  return (
    <div className="box-5-snaithe-main-info">
      <div className="image-container-right-1-5-custom">
        <img src={img3} alt="Square Image" />
      </div>
      <div className="text-container-5-custom">
        <div className="title-5-custom">Luxury Sports Car</div>
        <div className="text-5-custom1">
          Sleek, powerful, and meticulously maintained luxury sports car. Low
          mileage, excellent condition. Bid now for the thrill of the open road!
          <br />
          <br />
          With its sleek design and cutting-edge technology, this car is bound
          to turn heads wherever you go. From the roaring engine to the plush
          interior, every detail has been meticulously crafted to provide an
          exhilarating driving experience.
        </div>
        <a href="/bid">
          <h2 className="box3-second-titles-5-custom">Starting Bid: $0</h2>
        </a>
      </div>
    </div>
  );
};

export default Box3;
