import React from "react";
import "./Box4.css";
import img4 from "../../../assets/luxury sports mc 1.jpg";

const Box4 = () => {
  return (
    <div className="box-4">
      <div className="text-container-4">
        <div className="title-4">Luxury Sports Car</div>
        <div className="box-1-text-4">
          Sleek, powerful, and meticulously maintained luxury sports car. Low
          mileage, excellent condition. Bid now for the thrill of the open road!
          <br />
          <br />
          With its sleek design and cutting-edge technology, this car is bound
          to turn heads wherever you go. From the roaring engine to the plush
          interior, every detail has been meticulously crafted to provide an
          exhilarating driving experience.
        </div>
        <a href="/bid">
          <h2 className="box-second-titles-4">Starting Bid: $0</h2>
        </a>
      </div>
      <div className="image-container-right-4">
        <img src={img4} alt="Square Image" />
      </div>
    </div>
  );
};

export default Box4;
